const marked = require('marked');

export default {
  name: 'Doc',
  metaInfo: {
    title: 'CPFast |  | Chrome',
    meta: [
      {
        name: 'description',
        content: 'Vue3AntdTailwindCssnpm | Chrome'
      },
      {
        name: 'keywords',
        content: 'Vue3, Antd, TailwindCss, npm'
      }
    ]
  },
  data() {
    return {
      titles: ['', '', '', '', ''],
      articles: {},
      currentTitle: '',
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      const results = await Promise.all(this.titles.map(title => this.fetchMd(title)));
      const jsonRes = {};
      results.forEach(article => {
        jsonRes[article.title] = article.content;
      })
      this.articles = jsonRes;
    },
    goHome() {
      location.href = '/';
    },
    handleTitleChange(title) {
      this.currentTitle = title;
    },
    fetchMd(title) {
      return new Promise(resolve => {
        fetch(`./static/docs/${title}.md`)
          .then(response => response.text())
          .then(data => {
            const html = marked(data);
            resolve({ title, content: html });
          })
          .catch(error => console.error('Error fetching Markdown file:', error));
      })
    },
  }
}



